@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  background: #000;
}

#root {
  height: 100vh;
  width: 100vw;
}

/* Pinch-to-zoom enabled for volleyball field */
.volleyball-field {
  touch-action: pan-x pan-y pinch-zoom !important;
  -webkit-user-select: none;
  user-select: none;
}

/* Custom scrollbar styling voor court zoom container */
.overflow-auto::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

.overflow-auto::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.3);
}

.overflow-auto::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 4px;
}

.overflow-auto::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* Firefox scrollbar */
.overflow-auto {
  scrollbar-width: thin;
  scrollbar-color: rgba(255, 255, 255, 0.3) rgba(0, 0, 0, 0.3);
}

/* Mobile: maak ruimte voor compact sidebar rechts */
@media (max-width: 640px) {
  .main-content-area {
    padding-right: 90px !important; /* ruimte voor sidebar */
  }
  
  .compact-sidebar-mobile {
    position: fixed !important;
    right: 0 !important;
    top: 0 !important;
    height: 100vh !important;
    width: 80px !important;
    z-index: 40 !important;
  }
}
